.search-page {
  height: calc(100vh - 200px);
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  .search {
    padding-top: 30px;
    width: 45%;
    padding-right: 20px;
    display: flex;
    justify-content: flex-end;

    &__wrapper {
      max-width: 590px;
      width: 100%;
    }

    & form {
      position: relative;
      width: 100%;
      margin-bottom: 20px;

      & input {
        width: 100%;
        background: #C4C4C4;
        border-radius: 5px;
        display: block;
        font-size: 14px;
        padding: 12px 18px;

        &::placeholder {
          font-size: 14px;
        }
      }

      & label {
        left: 18px;
      }
    }

    &__categories {
      display: flex;
      justify-content: space-between;
      margin-bottom: 22px;

      &_item {
        width: 124px;
        background: #C4C4C4;
        border-radius: 5px;
        text-align: center;
        font-size: 14px;
        padding: 7px 0;
        cursor: pointer;
        @include transition-ease;

        @include on-event {
          background: darken(#C4C4C4, 10);
        }
      }
    }

    &__wrap {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      overflow-y: scroll;
      height: calc(100vh - 143px - 200px);
    }

    &__item {
      width: 170px;
      margin-bottom: 30px;
      cursor: pointer;

      &_img {
        height: 150px;
        overflow: hidden;
        width: 100%;
        position: relative;
        margin-bottom: 16px;
        border-radius: 5px;
        @include transition-ease;

        & .i {
          width: 100%;
          height: 100%;
          background-color: #C4C4C4;
          background-position: center;
          background-size: cover;
          background-repeat: no-repeat;
          @include transition-ease;
        }
      }

      @include on-event {

        .search__item_img {
          box-shadow: 0px 3px 6px 5px rgba(#000, 0.25);
        }

        .search__item_img .i {
          transform: scale(1.15);
        }
      }

      &_title {
        font-size: 14px;
        color: #000;
      }

      &_rating {
        color: #000;
        position: absolute;
        font-size: 18px;
        top: 6px;
        left: 8px;
        z-index: 3;
      }

      &_favorite {
        width: 20px;
        height: 18px;
        background-image: url('../img/heart.png');
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        position: absolute;
        z-index: 3;
        top: 9px;
        right: 8px;
      }
    }
  }

  & .map {
    width: 55%;
    height: calc(100vh - 200px);
  }
}